@import '../../styles/variables/grid';

.topbar {
  display: grid;
  grid-template-areas: 'image' 'leftContainer' 'rightContainer';

  @media #{$breakpoint-md-up} {
    grid-template-columns: 1fr auto;
    grid-template-areas: 'image navbar' 'leftContainer leftContainer' 'rightContainer rightContainer';
  }

  @media #{$breakpoint-xl-only} {
    grid-column-gap: 50px;
    grid-template-columns: repeat(10, 1fr);
    .image {
      grid-row: 1 / 1;
      grid-column: 1 / span 7;
    }
    .navbar {
      grid-row: 1 / 1;
      grid-column: 7 / span 4;
    }
    .leftContainer {
      grid-row: 2 / 2;
      grid-column: 1 / span 6;
    }
    .rightContainer {
      grid-row: 2 / 2;
      grid-column: 7 / span 4;
    }
  }

  @media #{$breakpoint-xxl-up} {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 160px repeat(7, 1fr);
    grid-row-gap: 40px;
    grid-column-gap: 40px;
    //grid-template-columns: min-content 0.9fr 1fr;
    //grid-template-areas: 'image leftContainer navbar' 'image leftContainer rightContainer';

    .image {
      grid-row: 1 / span 2;
      grid-column: 1 / span 5;
    }

    .navbar {
      grid-row: 1 / span 1;
      grid-column: 6 / span 3;
    }

    .leftContainer {
      grid-row: 1 / span 2;
      grid-column: 2 / span 4;
    }

    .rightContainer {
      grid-row: 2 / span 1;
      grid-column: 6 / span 3;

      display: flex;
      justify-content: flex-end;

      button {
        max-width: 500px;
      }
    }
  }
}

.image {
  grid-area: image;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .menuIcon {
    padding: 30px 20px;
    @media #{$breakpoint-md-up} {
      display: none;
    }
  }
}

.leftContainer {
  grid-area: leftContainer;
  display: grid;
  align-self: center;
}

.navbar {
  @media #{$breakpoint-sm-only-max} {
    display: none;
  }

  grid-area: navbar;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.rightContainer {
  grid-area: rightContainer;
  //align-self: center;
}
