@import '../../styles/variables/grid';
@import '../../styles/variables/colors';
@import '../../styles/variables/common';

.Footer {
  font-size: 0.8rem;

  @media #{$breakpoint-xs-only} {
    display: flex;
    flex-direction: column;
  }

  @media #{$breakpoint-sm-up} {
    display: flex;
    justify-content: space-between;
  }

  @media #{$breakpoint-xl-up} {
    font-size: 1rem;
  }

  .actionsContainer {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .footerLogo {
    margin: 30px 0;
    text-align: center;

    @media #{$breakpoint-sm-up} {
      flex: 0 0 auto;
      margin: 0;
      text-align: left;
    }

    .logo {
      display: none;
    }

    @media #{$breakpoint-lg-up} {
      .logo {
        display: block;
      }

      .logoSmall {
        display: none;
      }
    }
  }

  .footerLinks {

    @media #{$breakpoint-sm-up} {
      flex: 0 0 auto;
      align-self: center;
    }

    @media #{$breakpoint-xl-up} {
      display: flex;
      flex-wrap: nowrap;
    }

    a,
    p {
      margin-bottom: 14px;
      display: block;
      line-height: 2;
      color: $black;
      padding: 0;


      &:last-child {
        margin-bottom: 0;
      }

      @media #{$breakpoint-xl-up} {
        margin: 0 0 0 75px;
        text-transform: none;

        &:first-child {
          margin-left: 0;
        }
      }
    }

    a {
      cursor: pointer;
    }
  }
}
