@import '../../styles/variables/colors';

svg {
  display: inline-block;

  &.primary {
    fill: $primary;
  }

  &.secondary {
    fill: $secondary;
    stroke: $secondary;
  }

  &.disabled {
    fill: $icon-disabled;
    stroke: $icon-disabled;
  }

  &.black {
    fill: black;
    stroke: black;
  }

  &.white {
    fill: white;
    stroke: white;
  }
}
