@import '../../styles/variables/common';
@import '../../styles/variables/colors';
@import '../../styles/variables/grid';

.SidebarContainer {
  z-index: 100;
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
  width: $sidebar-size-desktop;
  transition: width $animation-duration;

  @media #{$breakpoint-sm-only-max} {
    width: 100vw !important;
    transition: width $animation-duration;

    .Sidebar {
      width: 100vw !important;
      height: 100% !important;
      transition: width $animation-duration;
    }

    &:not(.expanded) {
      width: 0 !important;
      transition: width $animation-duration;

      .Sidebar {
        width: 0 !important;
        transition: width $animation-duration;
      }
    }
  }

  &.mobileOnly {
    &:not(.expanded) {
      &,
      .Sidebar,
      .Sidebar .main {
        width: 0;
      }
    }

    @media #{$breakpoint-lg-up} {
      width: 0;
    }

    .organisation.selected:before {
      display: none;
    }
  }

  &.expanded {
    width: $sidebar-size-expanded-desktop;
    transition: width $animation-duration;

    .Sidebar {
      width: $sidebar-size-expanded-desktop;
      transition: width $animation-duration;
    }
  }

  .Sidebar {
    position: fixed;
    background-color: $gray-1;
    height: 100%;

    width: $sidebar-size-desktop;
    transition: width $animation-duration;

    display: grid;
    justify-content: end;

    .main {
      grid-area: sidebar-main;
      justify-self: end;
      background-color: $gray-1;
      display: grid;
      grid-template-rows: auto 1fr;
      width: 100%;
    }

    .content {
      grid-area: sidebar-content;
      overflow-x: hidden;
      min-width: calc(#{$sidebar-size-expanded-desktop} - #{$sidebar-size-desktop});
      display: grid;
      align-content: space-between;
      grid-template-rows: 1fr auto;
      grid-template-columns: minmax(1px, calc(#{$sidebar-size-expanded-desktop} - #{$sidebar-size-desktop}));
    }
  }

  &:not(.expanded) .Sidebar {
    grid-template-areas: 'sidebar-content sidebar-main ';
    grid-template-columns: 0 $sidebar-size-desktop;

    .main {
      @media #{$breakpoint-sm-only-max} {
        width: 0;
      }
    }

    .content {
      width: 0;
      display: none;
    }
  }

  &.expanded .Sidebar {
    grid-template-areas: 'sidebar-content sidebar-main';
    grid-template-columns: minmax(1px, calc(#{$sidebar-size-expanded-desktop} - #{$sidebar-size-desktop})) $sidebar-size-desktop;
  }
}

.SidebarContainer:not(.mobileMode) {
  .Sidebar {
  }
}

.SidebarContainer.mobileMode {
  height: 100vh;
  width: 100vw;

  &.expanded .Sidebar {
    height: 100vh;
    width: 100vw;
    right: 0;
    top: 0;

    z-index: 100;
  }
}

.mobileMode {
  display: initial;
}


.pages {
  max-width: 100%;

  ul {

    @media #{$breakpoint-xs-only} {
      white-space: pre-line;
      font-size: 4vw;
      line-height: 4vw;
    }

    li {
      margin-bottom: 1rem;
      cursor: pointer;
    }

    li:not(.active) {
      a {
        color: $black;
      }
    }
  }
}

.menu {
  .menuItem {
    position: relative;
    width: 107px;
    height: 70px;
    display: grid;
    align-items: center;
    justify-content: center;

    &:first-child {
      margin-top: 15px;
    }

    &:hover:not(.disabled) {
      svg {
        fill: red;
      }

      .label {
        display: initial;
      }
    }

    &:not(.disabled) {
      cursor: pointer;
    }

    &.disabled {
      cursor: not-allowed;
    }

    &.hidden {
      display: none;
    }

    .label {
      display: none;
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      text-align: center;
      font-size: 10px;
    }
  }
}

.organisationsControl {
  display: grid;
  justify-items: center;
  align-self: end;
}
