@import 'styles/typography/common';
@import 'styles/typography/fonts';
@import 'styles/typography/headers';

@import 'styles/structure/common';
@import 'styles/structure/header';
@import 'styles/structure/content';
@import 'styles/structure/footer';

@import 'styles/variables/common';
@import 'styles/variables/colors';
@import 'styles/variables/grid';
@import 'styles/variables/typography';
@import 'styles/variables/indention';

@import 'styles/form/form-group';

@import 'styles/modules/scales';

* {
  box-sizing: border-box;
}

html,
body,
p,
//h1,
//h2,
h3,
h4,
footer {
  margin: 0;
}

html,
body {}

body {
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;

  @media #{$breakpoint-sm-only-max} {
    margin-bottom: 3rem !important;
  }
}

div#root {}

ul,
li {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

.no-scroll {
  overflow: hidden !important;
}

.custom-scroll {
  @media #{$breakpoint-md-up} {
    ::-webkit-scrollbar {
      width: 4px;
      height: 4px;

      @media #{$breakpoint-md-up} {
        width: 10px;
        height: 10px;
      }
    }

    &.custom-scroll-black {
      ::-webkit-scrollbar {
        @media #{$breakpoint-md-up} {
          width: 3px;
          height: 3px;
        }
      }
    }

    ::-webkit-scrollbar-track {
      background-color: $white;
    }

    ::-webkit-scrollbar-thumb {

      @media #{$breakpoint-md-up} {
        background: $gray-2;
        border-radius: 0;
        border-color: white;
        border-style: solid;
        border-width: 3px;
      }
    }

    &.custom-scroll-black {
      ::-webkit-scrollbar-thumb {
        background: $black !important;
        border: none !important;
      }
    }

    ::-webkit-scrollbar-thumb:hover {
      opacity: 0.1;
      border-width: 2.5px;
    }


    scrollbar-width: thin;
    scrollbar-color: $gray-2 white !important;

    &>* {
      scrollbar-width: thin;
      scrollbar-color: $gray-2 white !important;
    }
  }
}

.bg-primary {
  background-color: $primary;
}

.bg-secondary {
  background-color: $secondary;
}

.bg-grey-1 {
  background-color: $gray-1;
}

.bg-grey-2 {
  background-color: $gray-2;
}

.bg-grey-3 {
  background-color: $gray-3;
}

.bg-grey-4 {
  background-color: $gray-4;
}

.color-primary {
  color: $primary;
}

.color-secondary {
  color: $secondary;
}

.color-grey-1 {
  color: $gray-1;
}

.color-grey-2 {
  color: $gray-2;
}

.color-grey-3 {
  color: $gray-3;
}

.with-radius {
  border-radius: $border-radius;
}
