@import '../../../../styles/variables/grid';

.StepDataEncryption {
  height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;

  @media #{$breakpoint-sm-up} {
    margin: 0 auto;
    width: 650px;
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media #{$breakpoint-md-up} {
      flex-direction: row;
      align-items: center;
    }
    .icon {
      display: flex;
      justify-content: center;
    }

    p {
      margin-top: 2rem;
      margin-left: 1rem;
    }
  }
}
