@import '../../styles/variables/grid';
@import '../../styles/variables/typography';
@import '../../styles/variables/colors';

.form-group {
  position: relative;
  margin-bottom: 0.5rem;
  padding-top: 1rem;
  width: 100%;

  @media #{$breakpoint-md-up} {
    padding-top: 25px;
  }

  label {
    position: absolute;
    top: 38px;
    left: 30px;
    font-size: $text-fields-font-size-mobile;
    line-height: $text-fields-line-height-mobile;
    color: $black;
    opacity: 0.2;
    cursor: text;
    z-index: 1;
    transition: all 0.4s;

    @media #{$breakpoint-md-up} {
      top: 49px;
      font-size: $text-fields-font-size-desktop;
      line-height: $text-fields-line-height-desktop;
    }
  }

  &__select,
  &.focused,
  &.filled {
    label {
      position: absolute;
      top: 0;
      font-size: $text-fields-label-font-size-mobile;
      line-height: $text-fields-label-line-height-mobile;
      color: $black;
      opacity: 1;
      cursor: default;

      @media #{$breakpoint-md-up} {
        left: 30px;
        font-size: $text-fields-label-font-size-desktop;
        line-height: $text-fields-label-line-height-desktop;
      }
    }
  }

  .description {
    margin-top: 3px;
    padding: 0 30px;
    font-size: $text-fields-help-font-size-mobile;
    line-height: $text-fields-help-line-height-mobile;
    color: $black;
    min-height: 1rem;

    @media #{$breakpoint-md-up} {
      font-size: $text-fields-help-font-size-desktop;
      line-height: $text-fields-help-line-height-desktop;
    }
  }

  &.focused {
    label {
      color: $black;
      opacity: 1;
    }
  }

  &.error {
    input[type='email'],
    input[type='password'],
    input[type='text'],
    input[type='number'],
    textarea {
      border-color: $error;
    }
    .description {
      color: $error-text;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__actions {
    a:not(.btn),
    span {
      margin: 0.5rem 0;
      padding: 0 1em;
      display: block;
      color: $black;
      text-align: center;
      align-self: center;
    }

    a:not(.btn) {
      padding: 0 1.5em;
    }
  }
}
