@import '../../styles/variables/colors';
@import '../../styles/variables/grid';
@import '../../styles/variables/indention';

.formGroup {
  label {
    left: 30px;

    position: absolute;
    top: 0;
    font-size: 16px;
    line-height: 20px;
    color: #171719;
    opacity: 1;
    cursor: default;
  }

  .formGroupRadio {
    padding: $space-mobile;
    background-color: $text-field-bg;
    border: 3px solid $text-field-bg;
    border-radius: 15px;
    box-sizing: border-box;

    @media #{$breakpoint-lg-up} {
      padding: $space-desktop;
    }

    label {
      position: relative;
      top: 0;
      left: 0;
      margin-bottom: 20px;
      padding-left: 80px;
      min-height: 60px;
      display: flex;
      align-items: center;
      opacity: 1;
      cursor: pointer;

      &:last-child {
        margin-bottom: 0;
      }

      input {
        display: none;

        &:checked + span:after {
          visibility: visible;
        }
      }

      span {
        cursor: pointer;

        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 60px;
          height: 60px;
          background: $white;
          border: 3px solid $gray-2;
          border-radius: 50%;
          box-sizing: border-box;
        }

        &:after {
          content: '';
          position: absolute;
          left: 26px;
          top: 26px;
          width: 8px;
          height: 8px;
          background: $black;
          border-radius: 50%;
          box-sizing: border-box;
          visibility: hidden;
        }
      }
    }
  }

  &.error .form-group__radio {
    border-color: $error;
  }
}
