@import '../../../styles/variables/colors';
@import '../../../styles/variables/grid';
@import '../../../styles/variables/common';

.TopbarProfileImage {
  width: 100%;
  display: grid;
  grid-template-columns: 80px 4fr;
  align-items: center;
  justify-content: center;

  @media #{$breakpoint-lg-up} {
    grid-template-columns: 160px 4fr;
  }

  .image {
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: $gray-1;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$breakpoint-lg-up} {
      width: 160px;
      height: 160px;
    }

    &:after {
      position: absolute;
      bottom: 0;
      height: 50%;
      width: 100%;
      text-transform: uppercase;
      background-color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      font-size: 16px;
      line-height: 19px;
      opacity: 0.8;
    }

    @media #{$breakpoint-xxl-up} {
      &:hover:after {
        content: 'Информация о себе';
      }
    }

    .social {
      margin-left: auto;
      position: absolute;
      top: 0;
      right: 0;
      width: 25px;
      height: 25px;

      @media #{$breakpoint-lg-up} {
        width: 45px;
        height: 45px;
      }
    }
  }

  .about {

    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $white;
    top: 0;
    left: 100%;
    height: 100%;
    transition: visibility $animation-duration, opacity $animation-duration;
    padding-left: 0.5rem;

    @media #{$breakpoint-md-only-max} {
      h3 {
        font-size: 0.7rem;
        line-height: 1rem;

      }
      font-size: 0.7rem;
      line-height: 1rem;
    }

    @media #{$breakpoint-md-up} {
      padding-left: 2rem;
    }

    @media #{$breakpoint-xxl-up} {
      z-index: 2;
      opacity: 0;
      visibility: collapse;
    }

    strong {
      margin-right: 1rem;
    }
  }

  .image:hover + .about {
    visibility: visible !important;
    opacity: 1;
    transition: visibility $animation-duration, opacity $animation-duration;
  }
}
