@import '../../../styles/variables/colors';
@import '../../../styles/variables/common';
@import '../../../styles/variables/grid';
@import '../../../styles/typography/common';
@import '../../../styles/variables/indention';

.TopSuggestions {
  display: grid;
  grid-template-rows: 3fr 1.5fr;

  padding: calc(#{ $space-mobile} * 0.5) 0;

  @media #{$breakpoint-lg-up} {
    padding: calc(#{ $space-desktop} * 0.5) $space-desktop;
  }

  @media #{$breakpoint-lg-up} {
    flex-direction: row;
    justify-content: space-between;

    grid-template-rows: 1fr;
    grid-template-columns: 1fr 600px;
  }

  .suggestionsList {
    .suggestion {
      display: grid;
      grid-template-columns: minmax(0, $space-desktop) minmax(0, calc(#{ $space-desktop} * 2)) 1fr;
      align-items: baseline;
      padding-top: 0.5rem;

      @media #{$breakpoint-lg-up} {
        grid-template-columns: $space-desktop calc(#{ $space-desktop} * 3) 1fr;
      }

      .icon {
        //display: grid;
        //align-self: center;
        display: flex;
        align-items: baseline;
        cursor: pointer;
        height: 1rem;
      }

      .likesCount {
        span {
          background-color: $white;
          border-radius: $border-radius;
          padding: 5px 10px;
        }
      }

      .suggestionText {
        cursor: pointer;

        &:hover {
          opacity: 0.8;
        }
      }
    }
  }

  .rightContainer {
    max-width: 600px;

    @media #{$breakpoint-md-only-max} {
      h3 {
        font-size: $font-size-mobile;
        line-height: $line-height-mobile;
        text-transform: initial;
        font-family: "Jost", sans-serif;
        font-weight: initial;
      }
    }

    .rightContainerContent {
      position: relative;
      height: 100%;

      .collapsed {
        opacity: 0;
        visibility: collapse;
        transition: visibility 250ms, opacity 250ms;
      }

      .suggestionInfo {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }

      .suggest {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-bottom: 0.5rem;
      }
    }


  }
}