@import '../../styles/variables/common';
@import '../../styles/variables/colors';
@import '../../styles/variables/grid';

.background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 50;
}
.GlobalModal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;

  main {
    position: relative;
    background-color: $white;
    border-radius: $border-radius;
    min-width: 20vw;
    min-height: 20vh;
    max-width: 80vw;
    max-height: 80vh;

    .close {
      position: absolute;
      top: -32px;
      right: -32px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 70px;
      height: 70px;
      cursor: pointer;

      background-color: white;
      border-radius: 50%;
    }
  }
}
