@import '../../../styles/variables/colors';
@import '../../../styles/variables/common';
@import '../../../styles/variables/grid';
@import '../../../styles/variables/indention';

.SuggestionForm {
  position: relative;
  min-height: 500px;
  background-color: $gray-1;
  border-radius: $border-radius;
  display: grid;

  padding: $space-mobile;
  @media #{$breakpoint-lg-up} {
    padding: calc(#{ $space-desktop} * 1.5) calc(#{ $space-desktop} * 2);
  }

    grid-template-rows: repeat(3, auto);
    grid-template-columns: 1fr;
    grid-template-areas: 'container' 'info' 'backButton';
    grid-column-gap: 0;

  @media #{$breakpoint-lg-up} {
    grid-template-areas: 'container container' 'backButton info';
    grid-template-rows: auto 1fr;
    grid-template-columns: minmax(auto, 640px)  minmax(auto, 600px);
    justify-content: space-between;
    grid-column-gap: $space-desktop;
  }
}

.container {
  grid-area: container;
  display: flex;

  .text {
    margin-top: 0;
  }

  @media #{$breakpoint-md-only-max} {
    & {
      flex-direction: column;
    }
    .image {
      min-height: 200px;
      min-width: 100%;
    }
  }
}
.backButton {
  grid-area: backButton;
  max-width: 640px;
  align-self: end;
}

.info {
  grid-area: info;
  align-self: end;
  max-width: 600px;
}

.close {
  display: inline-flex;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}
