@import '../../styles/variables/grid';
@import '../../styles/variables/typography';
@import '../../styles/variables/colors';

button {
  padding: 0 20px;
  display: block;
  width: 100%;

  background: $white;
  border: 3px solid $primary;
  border-radius: 15px;
  box-sizing: border-box;
  cursor: pointer;
  min-height: 60px;

  font-family: "Jost Medium", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: $button-font-size-mobile;
  line-height: $button-line-height-mobile;
  color: $black;
  text-align: center;

  &:not(.text-uppercase) {
    font-size: $button-font-size;
    line-height: $button-line-height;
  }

  &.text-uppercase {
    font-size: $button-font-size-mobile;
    line-height: $button-line-height-mobile;

    @media #{$breakpoint-md-up} {
      height: 60px;
      font-size: $button-font-size-desktop;
      line-height: $button-line-height-desktop;
    }
  }


  &.with-icon {
    svg {
      display: none;
    }

    @media #{$breakpoint-md-up} {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 50px;
      padding-right: 50px;
      position: relative;

      &.icon-right {
        svg {
          display: initial;
          position: absolute;
          right: 50px;
        }
      }

      &.icon-left {
        justify-content: flex-end;

        svg {
          display: initial;
          position: absolute;
          left: 50px;
        }
      }
    }
  }

  &:hover {
    color: $white;
    background: $primary;
    opacity: 0.7;

    & svg {
      fill: $white;
    }
  }

  &:disabled {
    color: $black;
    background: $white;
    opacity: 0.2;
  }

  &.primary {
    color: $white;
    background: $primary;

    svg {
      fill: $white;
    }

    &:hover {
      color: $white;
      background: $primary;
      opacity: 0.7;
    }

    &:disabled {
      color: $white;
      background: $primary;
      opacity: 0.2;
    }
  }
}